@import 'vue-cal/dist/vuecal.css';
:root {
  --iande-border-radius: 10px;
  --iande-primary-color: #A8DBBC;
  --iande-secondary-color: #1E2E55;
  --iande-tertiary-color: #7DB6C5;
  --iande-background-color: white;
  --iande-lighter-color: #F5F5F5;
  --iande-light-color: #DDDDDD;
  --iande-medium-color: #999999;
  --iande-text-color: #666666;
  --iande-error-color: #E53E3E;
  --iande-success-color: #238B19;
  --iande-text-font: Open Sans, sans-serif;
  --iande-title-font: Quicksand, Open Sans, sans-serif;
  --iande-accent-color: var(--iande-secondary-color);
}

body {
  margin: 0;
}

.iande {
  accent-color: var(--iande-accent-color);
  background-color: var(--iande-background-color);
  color: var(--iande-text-color);
  font-family: var(--iande-text-font);
  font-size: 16px;
  line-height: 1.375;
}
.iande *, .iande *::before, .iande *::after {
  box-sizing: border-box;
}
.iande .iande-container {
  margin-left: auto;
  margin-right: auto;
  max-width: 85em;
  padding: 0 1em;
  width: 100%;
}
.iande .iande-container .iande-container {
  padding: 0;
}
.iande .iande-container.narrow {
  max-width: 30em;
}
.iande .iande-container.full-width {
  max-width: 100%;
}
.iande .iande-print-page {
  margin: auto;
  width: 100%;
}
@media screen {
  .iande .iande-print-page {
    max-width: 297mm;
    padding: 30px;
  }
}
@media screen and (max-width: 800px) {
  .iande .iande-print-page {
    padding: 30px 20px;
  }
}
.iande article {
  margin-bottom: 40px;
}
.iande h1 {
  color: var(--iande-primary-color);
  font-family: var(--iande-title-font);
  font-size: 2.5em;
  font-weight: normal;
  margin: 0 0 1em;
  text-align: center;
}
.iande .slogan {
  font-family: var(--iande-title-font);
  font-size: 1.25em;
  text-align: center;
}
.iande p {
  font-size: 1em;
}

.iande .text-center {
  text-align: center;
}
.iande .text-secondary {
  color: var(--iande-secondary-color);
}
.iande .text-sm {
  font-size: 0.875em;
}
.iande .mt-lg {
  margin-top: 40px;
}

@font-face {
  font-family: "Open Sans";
  font-weight: normal;
  src: url("../assets/fonts/OpenSans-Regular.ttf") format("truetype");
}
@font-face {
  font-family: "Open Sans";
  font-weight: bold;
  src: url("../assets/fonts/OpenSans-Bold.ttf") format("truetype");
}
@font-face {
  font-family: "Quicksand";
  font-weight: 500;
  src: url("../assets/fonts/Quicksand-VariableFont_wght.ttf") format("truetype");
}
.iande-button {
  border: 1px solid var(--iande-primary-color);
  border-radius: var(--iande-border-radius);
  color: var(--iande-secondary-color);
  cursor: pointer;
  display: block;
  font-size: 1em;
  font-weight: bold;
  padding: 15px 20px;
  text-align: center;
  text-decoration: none;
  text-transform: none;
  width: 100%;
}
.iande-button.small {
  height: 100%;
  padding: 10px;
  width: auto;
}
.iande-button.disabled {
  background-color: var(--iande-light-color);
  border-color: var(--iande-light-color);
  color: var(--iande-text-color);
  cursor: default;
  opacity: 0.75;
}
.iande-button.solid {
  background-color: var(--iande-light-color);
  border-color: var(--iande-light-color);
}
.iande-button.primary {
  background-color: var(--iande-primary-color);
}
.iande-button.secondary {
  background-color: var(--iande-secondary-color);
  border-color: var(--iande-secondary-color);
  color: var(--iande-primary-color);
}
.iande-button.outline {
  background-color: transparent;
}

.iande-form .iande-form-grid {
  display: grid;
  grid-gap: 20px;
  grid-template-columns: 1fr 1fr;
  margin: 10px 0;
}
@media screen and (max-width: 600px) {
  .iande-form .iande-form-grid {
    grid-template-columns: 1fr;
  }
}
.iande-form .iande-label {
  color: var(--iande-secondary-color);
  font-size: 1em;
  font-weight: bold;
}
.iande-form .iande-label + .iande-field {
  margin-top: 0.5em;
}
.iande-form .iande-label__optional {
  color: var(--iande-primary-color);
  font-size: 0.875em;
  font-weight: bold;
  float: right;
}
.iande-form .iande-hint {
  display: block;
  margin: 5px 0;
}
.iande-form .iande-hint b {
  color: var(--iande-secondary-color);
}
.iande-form .iande-input {
  border: 2px solid var(--iande-light-color);
  border-radius: var(--iande-border-radius);
  background-color: var(--iande-background-color);
  color: var(--iande-text-color);
  display: block;
  font-family: inherit;
  font-size: 1em;
  height: fit-content;
  padding: 15px 20px;
  width: 100%;
}
.iande-form .iande-input.invalid {
  border-color: var(--iande-error-color);
}
.iande-form .iande-input:disabled {
  background-color: var(--iande-lighter-color);
}
.iande-form .iande-input::placeholder {
  color: var(--iande-medium-color);
}
.iande-form .iande-radio-group {
  display: flex;
  flex-wrap: wrap;
}
.iande-form .iande-radio-group.columns {
  flex-direction: column;
  margin: 0;
}
@media screen and (max-width: 600px) {
  .iande-form .iande-radio-group {
    flex-direction: column;
  }
}
.iande-form .iande-radio {
  align-items: center;
  display: flex;
  margin: 10px 20px 0 0;
}
.iande-form .iande-radio input {
  margin: 0 7.5px 0 0;
  transform: scale(1.25);
}
.iande-form .iande-form-error {
  color: var(--iande-error-color);
  text-align: center;
}
.iande-form .iande-field .iande-form-error {
  font-size: 0.875em;
  margin: 0.25em var(--iande-border-radius) 0;
  text-align: start;
}
.iande-form .iande-form-message {
  color: var(--iande-success-color);
  font-size: 0.875em;
  margin: 5px var(--iande-border-radius) 0;
}
.iande-form .iande-form-message.-error {
  color: var(--iande-error-color);
}
.iande-form .iande-complex-field {
  background-color: var(--iande-lighter-color);
  border-radius: var(--iande-border-radius);
  margin-top: 20px;
  padding: 1em;
}
.iande-form .iande-form-link {
  color: var(--iande-secondary-color);
  display: block;
  font-size: 0.875em;
  text-align: end;
  text-decoration: underline;
}
.iande-form .iande-form-link:hover {
  text-decoration: none;
}

.iande-stack.stack-sm > * + * {
  margin-bottom: 0;
  margin-top: 10px;
}
.iande-stack.stack-md > * + * {
  margin-bottom: 0;
  margin-top: 20px;
}
.iande-stack.stack-lg > * + * {
  margin-bottom: 0;
  margin-top: 40px;
}

.iande-add-item {
  align-items: center;
  display: flex;
}
.iande-add-item > span {
  align-items: center;
  background-color: var(--iande-primary-color);
  border-radius: 50%;
  color: var(--iande-secondary-color);
  display: flex;
  height: calc(1em + 20px);
  justify-content: center;
  width: calc(1em + 20px);
}
.iande-add-item .iande-label {
  margin-left: 1em;
}

.iande-groups-legend__entry.assigned-other:before, .iande-educator-agenda__bubble.assigned-other, .iande-educator-agenda__event.assigned-other:before {
  background-color: var(--iande-tertiary-color);
  border-color: var(--iande-tertiary-color);
  color: white;
}

.iande-groups-legend__entry.assigned-self:before, .iande-educator-agenda__bubble.assigned-self, .iande-educator-agenda__event.assigned-self:before {
  background-color: var(--iande-success-color);
  border-color: var(--iande-success-color);
  color: white;
}

.iande-groups-legend__entry.unassigned:before, .iande-educator-agenda__bubble.unassigned, .iande-educator-agenda__event.unassigned:before {
  background-color: inherit;
  border-color: var(--iande-success-color);
  color: var(--iande-success-color);
}

.iande-educator-agenda__month-row {
  align-items: center;
  display: flex;
  justify-content: center;
  text-align: center;
}
.iande-educator-agenda__bubble {
  align-items: center;
  border-radius: 50%;
  border-style: solid;
  border-width: 1px;
  display: inline-flex;
  font-size: 12px;
  height: 18px;
  justify-content: center;
  line-height: 1;
  margin: 5px;
  width: 18px;
}
@media screen and (max-width: 700px) {
  .iande-educator-agenda__bubble {
    height: 10px;
    font-size: 0;
    margin: 3px;
    width: 10px;
  }
}
.iande-educator-agenda__event {
  align-items: center;
  background-color: var(--iande-primary-color);
  color: var(--iande-secondary-color);
  display: flex;
  flex-direction: column;
  font-size: 0.75em;
  height: 100%;
  justify-content: center;
  position: relative;
}
.iande-educator-agenda__event p {
  margin: 4px;
}
.iande-educator-agenda__event a {
  color: inherit;
  font-weight: bold;
}
.iande-educator-agenda__event:before {
  border-radius: 50%;
  border-style: solid;
  border-width: 1px;
  content: "";
  display: block;
  height: 8px;
  left: 5px;
  position: absolute;
  top: 5px;
  width: 8px;
}
.iande-appointment {
  padding: 0;
}
.iande-appointment__summary {
  background: var(--iande-lighter-color);
  border-radius: var(--iande-border-radius) var(--iande-border-radius) 0 0;
  display: flex;
  line-height: 1.2;
  padding: 20px;
}
.iande-appointment__summary.collapsed {
  border-radius: var(--iande-border-radius);
}
@media screen and (max-width: 400px) {
  .iande-appointment__summary {
    flex-direction: column;
  }
}
.iande-appointment__summary > :nth-child(2) {
  width: 100%;
}
@media screen and (max-width: 800px) {
  .iande-appointment__summary > :nth-child(2) {
    flex-direction: column;
  }
}
.iande-appointment__date {
  align-items: center;
  color: var(--iande-secondary-color);
  display: flex;
  flex-direction: column;
  font-family: var(--iande-title-font);
  justify-content: center;
  text-align: center;
}
@media screen and (max-width: 400px) {
  .iande-appointment__date {
    border-bottom: 1px solid var(--iande-medium-color);
    padding-bottom: 10px;
  }
}
@media screen and (min-width: 401px) {
  .iande-appointment__date {
    border-right: 1px solid var(--iande-medium-color);
    margin-right: 20px;
    padding-right: 20px;
  }
}
.iande-appointment__from {
  font-size: 0.875em;
}
.iande-appointment__day {
  font-size: 2em;
}
.iande-appointment__month {
  font-size: 1.5em;
}
.iande-appointment__summary-row {
  align-items: center;
  display: flex;
  justify-content: space-between;
}
.iande-appointment__summary-row > :nth-child(2) {
  align-items: center;
  display: flex;
  flex: 1;
  justify-content: flex-end;
}
@media screen and (max-width: 900px) {
  .iande-appointment__summary-row {
    align-items: flex-start;
    flex-direction: column;
  }
  .iande-appointment__summary-row > :nth-child(2) {
    width: 100%;
  }
}
@media screen and (max-width: 400px) {
  .iande-appointment__summary-row > :nth-child(1) {
    width: 100%;
  }
}
.iande-appointment h2 {
  color: var(--iande-secondary-color);
  font-size: 1.5em;
  font-weight: bold;
  margin: 0;
}
@media screen and (max-width: 400px) {
  .iande-appointment__summary-main {
    padding-top: 10px;
    text-align: center;
  }
}
.iande-appointment__info {
  margin-top: 10px;
}
.iande-appointment__info svg[data-icon] {
  color: var(--iande-tertiary-color);
  margin-right: 5px;
}
.iande-appointment__toggle {
  color: var(--iande-secondary-color);
  font-size: 1.5em;
  margin-left: 20px;
}
@media screen and (max-width: 600px) {
  .iande-appointment__toggle {
    display: none;
  }
}
.iande-appointment__toggle-button {
  align-items: center;
  background-color: var(--iande-secondary-color);
  border-radius: 0 0 var(--iande-border-radius) var(--iande-border-radius);
  color: white;
  display: none;
  font-size: 0.75em;
  font-weight: bold;
  justify-content: center;
  padding: 5px;
  text-transform: lowercase;
}
@media screen and (max-width: 600px) {
  .iande-appointment__toggle-button {
    display: flex;
  }
}
.iande-appointment__details {
  background-color: var(--iande-lighter-color);
  padding: 20px;
}
@media screen and (min-width: 601px) {
  .iande-appointment__details {
    border-radius: 0 0 var(--iande-border-radius) var(--iande-border-radius);
  }
}
.iande-appointment__boxes {
  display: grid;
  grid-gap: 20px;
  grid-template-columns: repeat(auto-fill, minmax(24em, 1fr));
}
@media screen and (max-width: 30em) {
  .iande-appointment__boxes {
    grid-template-columns: 1fr;
  }
}
.iande-appointment__box {
  font-size: 0.875em;
}
.iande-appointment__box > * {
  padding: 10px;
}
.iande-appointment__box > * + * {
  border-top: 1px solid var(--iande-light-color);
}
.iande-appointment__box-title {
  align-items: flex-end;
  border-bottom: 3px solid var(--iande-tertiary-color);
  display: flex;
  justify-content: space-between;
}
.iande-appointment__box-title h3 {
  color: var(--iande-secondary-color);
  font-size: 1.25em;
  margin: 0;
}
.iande-appointment__box-title h3 svg[data-icon] {
  color: var(--iande-tertiary-color);
  margin-right: 10px;
}
.iande-appointment__edit {
  color: var(--iande-medium-color);
  font-size: 0.75em;
  margin-left: 10px;
}
.iande-appointment__edit a {
  color: inherit;
}
.iande-appointment__feedback-link {
  border-top: none;
  padding: 0;
}
.iande-appointment__feedback-link a {
  background-color: var(--iande-primary-color);
  border-radius: var(--iande-border-radius);
  color: var(--iande-secondary-color);
  display: block;
  font-weight: bold;
  line-height: 1;
  padding: 10px;
  text-align: center;
  text-decoration: none;
}
.iande-appointment__buttons {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;
  margin-top: 10px;
}
.iande-appointment__buttons .iande-button {
  margin-top: 1em;
  width: auto;
}
.iande-appointment__buttons .iande-button + .iande-button {
  margin-left: 20px;
}
.iande-appointment__buttons .iande-button svg[data-icon] {
  margin-left: 10px;
}

.iande-appointments-toolbar {
  align-items: center;
  display: flex;
  flex-wrap: wrap-reverse;
  justify-content: space-between;
}

.iande-appointments-filter {
  border: none;
  margin: 0;
}
.iande-appointments-filter__label {
  font-weight: bold;
}
.iande-appointments-filter__row {
  display: inline-flex;
  flex-wrap: wrap;
  padding: 5px;
}
.iande-appointments-filter__row:focus-within {
  outline-style: auto;
}
.iande-appointments-filter input[type=radio] {
  height: 0;
  opacity: 0;
  width: 0;
}
.iande-appointments-filter input[type=radio] + label {
  margin-left: 1em;
  padding-bottom: 5px;
}
.iande-appointments-filter input[type=radio]:checked + label {
  border-bottom: 3px solid var(--iande-tertiary-color);
}

.iande-file-uploader {
  display: flex;
  flex-wrap: wrap;
}
.iande-file-uploader input[type=file] {
  display: none;
}
.iande-file-uploader__thumbnail {
  margin-right: 20px;
}
.iande-file-uploader__thumbnail img {
  height: 200px;
  object-fit: cover;
  width: 200px;
}
.iande-file-uploader > :nth-child(2) {
  align-items: flex-start;
  display: flex;
  flex: 1;
  flex-direction: column;
  justify-content: flex-end;
  margin: 10px 0;
}
.iande-file-uploader > :nth-child(2) .iande-button {
  height: auto;
  margin-top: 10px;
}

.iande-group__summary h2.assigned-other:before {
  background-color: var(--iande-tertiary-color);
  border-color: var(--iande-tertiary-color);
  color: white;
}

.iande-group__summary h2.assigned-self:before {
  background-color: var(--iande-success-color);
  border-color: var(--iande-success-color);
  color: white;
}

.iande-group__summary h2.unassigned:before {
  background-color: inherit;
  border-color: var(--iande-success-color);
  color: var(--iande-success-color);
}

.iande-groups .iande-group {
  border-bottom: 1px solid var(--iande-light-color);
  padding-bottom: 40px;
}
.iande-groups .iande-group-title {
  border-bottom: 1px solid var(--iande-medium-color);
  color: var(--iande-text-color);
  font-size: 1em;
  padding-bottom: 6px;
  text-align: center;
  text-transform: uppercase;
}

.iande-group__summary h2 {
  color: var(--iande-secondary-color);
  font-size: 1.5em;
  font-weight: bold;
  margin: 0;
}
.iande-group__summary h2:before {
  border-radius: 50%;
  border-style: solid;
  border-width: 1px;
  content: "";
  display: inline-block;
  margin: 0.125em 0.35em 0.125em 0;
  min-height: 0.5em;
  min-width: 0.5em;
}
@media screen and (max-width: 900px) {
  .iande-group__summary-row {
    align-items: flex-start;
    flex-direction: column;
  }
  .iande-group__summary-row > :nth-child(2) {
    width: 100%;
  }
}
.iande-group__steps {
  align-items: center;
  display: flex;
  flex-wrap: wrap;
}
.iande-group__steps > * {
  margin: 0.5em 1em 0.5em 0;
}
.iande-group__step {
  align-items: center;
  color: var(--iande-tertiary-color);
  display: flex;
  font-size: 0.75em;
  font-weight: bold;
}
.iande-group__step-icon {
  color: var(--iande-medium-color);
  font-size: 2.5em;
  margin-right: 0.5ch;
}
.iande-group__step-icon.active {
  color: var(--iande-tertiary-color);
}
.iande-group__step label [data-icon] {
  color: var(--iande-secondary-color);
}
.iande-group__step select {
  background-color: transparent;
  border: none;
  color: var(--iande-secondary-color);
  font-size: inherit;
  font-weight: inherit;
  max-width: 11em;
}
.iande-group__details {
  padding: 20px;
}
.iande-group.boxed .iande-group__summary {
  background-color: var(--iande-lighter-color);
  border-radius: var(--iande-border-radius) var(--iande-border-radius) 0 0;
}
@media screen and (min-width: 601px) {
  .iande-group.boxed .iande-group__summary.collapsed {
    border-radius: var(--iande-border-radius);
  }
}
.iande-group.boxed .iande-group__details {
  background-color: var(--iande-lighter-color);
}
@media screen and (min-width: 601px) {
  .iande-group.boxed .iande-group__details {
    border-radius: 0 0 var(--iande-border-radius) var(--iande-border-radius);
  }
}
.iande-modal .iande-group__summary {
  background-color: var(--iande-lighter-color);
}

.iande-groups-legend {
  background-color: var(--iande-lighter-color);
  border-radius: var(--iande-border-radius);
  display: flex;
  flex-wrap: wrap;
  line-height: 2;
  padding: 10px 20px;
}
.iande-groups-legend > * {
  margin-right: 2em;
}
.iande-groups-legend__label {
  font-weight: bold;
}
.iande-groups-legend__entry {
  align-items: center;
  display: flex;
}
.iande-groups-legend__entry:before {
  border-radius: 50%;
  border-style: solid;
  border-width: 1px;
  content: "";
  display: inline-block;
  height: 0.75em;
  margin-right: 0.5em;
  width: 0.75em;
}
.iande-institutions {
  display: grid;
  grid-gap: 20px;
  grid-template-columns: repeat(auto-fill, minmax(24em, 1fr));
}
@media screen and (max-width: 30em) {
  .iande-institutions {
    grid-template-columns: 1fr;
  }
}

.iande-institution {
  padding: 0;
}
.iande-institution__summary {
  background: var(--iande-lighter-color);
  border-radius: var(--iande-border-radius) var(--iande-border-radius) 0 0;
  display: flex;
  line-height: 1.2;
  padding: 20px;
}
.iande-institution__summary.collapsed {
  border-radius: var(--iande-border-radius);
}
.iande-institution__summary > * {
  align-items: center;
  display: flex;
}
.iande-institution__summary h2 {
  color: var(--iande-secondary-color);
